<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
<!--    <cus-btn msg="欢喜"></cus-btn>-->
  </div>
</template>

<script>
import CusButton from './CusButton'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Visit Yixi Health Management.'
