<template>
  <button class="cus-btn">
    <span>{{msg}}</span>
  </button>
</template>

<script>
export default {
  name: 'CusButton',
  data: function () {
    return {
      msg: '下载'
    }
  },
  props: {
    // eslint-disable-next-line vue/no-dupe-keys
    msg: {
      default: '设置'
    }
  }
}
</script>

<style scoped>

</style>



